@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  height: 100%;
  width: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
li:hover span{
  color: #000;
}

h2{
  font-weight: bold;
  text-align: center;
  font-size: 20px;
  margin:0;
  margin-bottom: 10px;
  text-transform: uppercase;
}

.ant-layout .ant-layout-sider{
  background: #fff0ec;
}

.layout{
  height: 100vh;
}
.layout-content{
  overflow: auto;
  scroll-behavior: smooth;
}
.layout-footer{
  line-height: 30px;
  text-align: center;
  height:30px;
  padding:0;
}
.form-wrapper{
  width: 100%;
}

.image-preview{
  width:30%;
  margin: 10px auto;
}

.product-thumbnail-list{
  width:6vh;
}

@media only screen and (min-width: 768px) {
  /* For desktop: */
  .product-thumnail-list{
    width:6%;
  }
}
